* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  display: grid;
  place-items: center;
  background-image: url('./banner.png');
  font-size: 20px;
}
#logo{
  width: 300px;
}

.logos{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.logo{
  width: 100px;
}

#wheelOfFortune {
  display: inline-block;
  position: relative;
  overflow: hidden;
}

#wheel {
  display: block;
}

#spin {
  max-width: 125px;
  max-height: 125px;
  font: 1.5em/0 sans-serif;
  user-select: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 50%;
  margin-left: -13.7%;
  margin-top: -16%;
  background: #fff;
  color: #fff;
  box-shadow: 0 0 0 8px currentColor, 0 0px 15px 5px rgba(0, 0, 0, 0.6);
  border-radius: 50%;
  transition: 0.8s;
}

#spin::after {
  content: '';
  position: absolute;
  top: -17px;
  border: 10px solid transparent;
  border-bottom-color: currentColor;
  border-top: none;
}

.wheel-table {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;

}

#table {
  color: #fff;
}

#topic {
  padding-right: 20px;
  
}